import request from '@/utils/axios'

export function fetchDataStream(params) {
  return request({
    url: '/api/data/fetchDataStream',
    method: 'GET',
    params
  })
}

export function fetchStreamData(params) {
  return request({
    url: '/api/data/fetchStreamData',
    method: 'GET',
    params
  })
}

export function fetchLatestData(data) {
  return request({
    url: '/api/data/fetchLatestData',
    method: 'POST',
    data
  })
}

export function addDataStream(data) {
  return request({
    url: '/api/data/addDataStream',
    method: 'POST',
    data
  })
}

export function deleteDataStream(params) {
  return request({
    url: '/api/data/deleteDataStream',
    method: 'DELETE',
    params
  })
}

