<template>
  <div id="stream-container" v-loading="isLoading">
    <edit-drawer
      :drawer-switch="drawerSwitch"
      :drawer-title="drawerTitle"
      :stream-edit-data="streamEditDataObj.streamEditData"
      @add-success="handleAddDataStreamSuccess"
      @close="handleClose"
    />
    <main-app-header :page-title="pageTitle" />
    <div id="header-container" class="context-container">
      <div id="text-header">
        <div id="text-header-title" class="text">数据流数量(个)</div>
        <div id="text-header-value" class="text">{{ streamCount }}</div>
      </div>
      <el-button type="primary" @click="handleClickAdd"><el-icon><ElementPlus /></el-icon><span>添加数据流模板</span></el-button>
    </div>
    <div id="body-container" class="context-container">
      <el-table :data="streamList" style="width: 100%">
        <el-table-column prop="dataTemplateId" label="数据流ID" />
        <el-table-column prop="dataName" label="数据流名称" />
        <el-table-column prop="unitName" label="单位名称" />
        <el-table-column prop="unitSymbols" label="单位符号" />
        <el-table-column label="操作">
          <template #default="scope">
            <el-button link type="primary" size="small" @click="handleClickEdit(scope.$index)">编辑</el-button>
            <el-popconfirm
              confirm-button-type="danger"
              confirm-button-text="确认"
              cancel-button-text="取消"
              title="确认删除?"
              @confirm="handleClickDel(scope.$index)"
              @cancel="handleCancel"
            >
              <template #reference>
                <el-button link type="primary" size="small">删除</el-button>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <div id="pagination">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="streamCount"
          :page-size="8"
          @current-change="(e) => getDataStream(e, 8)"
        />
      </div>
    </div>
  </div>
</template>

<script setup>
import MainAppHeader from '@/components/mainAppHeader'
import { useRouterStore } from '@/store/router'
import { ref, reactive, onMounted } from 'vue'
import { fetchDataStream, deleteDataStream } from '@/apis/dataStream'
import { useRoute } from 'vue-router'
import EditDrawer from '@/views/dataStream/component/editDrawer'
import { ElMessage } from 'element-plus'

onMounted(() => {
  getDataStream()
})

const routerStore = useRouterStore()
const route = useRoute()
const pageTitle = ref(route.meta.title)
const streamCount = ref(0)
const streamList = reactive([])
const isLoading = ref(false)
function getDataStream(page = 1, pageSize = 8) {
  isLoading.value = true
  fetchDataStream({ productId: routerStore.productId, page, pageSize }).then(res => {
    if (res.code === 200) {
      streamCount.value = res.data.totalStream
      streamList.splice(0)
      streamList.push(...res.data.streamList)
    } else if (res.code === 500) {
      ElMessage({
        type: 'error',
        message: res.message
      })
    }
  }).finally(() => {
    isLoading.value = false
  })
}

const drawerSwitch = ref(false)
const drawerTitle = ref('添加数据流')
function handleClickAdd() {
  drawerSwitch.value = !drawerSwitch.value
}
function handleAddDataStreamSuccess() {
  let message
  if (streamEditDataObj.isEdit === false) {
    message = '添加成功'
  } else {
    message = '修改成功'
  }
  console.log(streamEditDataObj.streamEditData)
  ElMessage({
    type: 'success',
    message
  })
  getDataStream()
}

const streamEditDataObj = reactive({
  isEdit: false,
  streamEditData: {}
})
function handleClickEdit(index) {
  streamEditDataObj.isEdit = true
  streamEditDataObj.streamEditData.dataTemplateId = streamList[index].dataTemplateId
  streamEditDataObj.streamEditData.dataName = streamList[index].dataName
  streamEditDataObj.streamEditData.unitSymbols = streamList[index].unitSymbols
  streamEditDataObj.streamEditData.unitName = streamList[index].unitName
  streamEditDataObj.streamEditData.dataTemplateId = streamList[index].dataTemplateId
  drawerSwitch.value = !drawerSwitch.value
}
function handleClose() {
  streamEditDataObj.isEdit = false
  streamEditDataObj.streamEditData = {}
}
function handleCancel() {
  console.log('cancel')
}
function handleClickDel(index) {
  deleteDataStream({ productId: routerStore.productId, dataTemplateId: streamList[index].dataTemplateId }).then(res => {
    if (res.code === 200) {
      ElMessage({
        type: 'success',
        message: '删除成功'
      })
      getDataStream()
    }
  }, () => {
    ElMessage({
      type: 'error',
      message: '网络错误'
    })
  })
}
</script>

<style scoped>
.context-container {
  background-color: white;
  border-radius: 5px;
  margin-top: 1em;
  padding: 1.5em 1em;
}
#header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
#text-header-title {
  margin-bottom: 1em;
  color: #74777a;
  font-size: 0.8em;
}
.text {
  text-align: center;
  font-size: 1.5em;
}
#text-header~button {
  align-self: center;
}
#pagination {
  display: flex;
  justify-content: right;
  margin-right: 8%;
  padding-bottom: 2em;
}
</style>
