<template>
  <el-drawer v-model="isShowDrawer" :direction="'rtl'" @close="handleClose(dataStreamFrom)">
    <template #header>
      <h4>{{ drawerTitle }}</h4>
    </template>
    <template #default>
      <el-form ref="dataStreamFrom" :model="streamData" :label-position="'top'" :rules="rules">
        <el-form-item label="数据流名称" prop="dataName">
          <el-input v-model="streamData.dataName" :disabled="isEdit" placeholder="不超过20个字符，只能使用 字母、数字和_" />
        </el-form-item>
        <el-form-item label="单位名称" prop="unitName">
          <el-input v-model="streamData.unitName" placeholder="请输入单位名称，不超过20个字符" />
        </el-form-item>
        <el-form-item label="单位符号" prop="unitSymbols">
          <el-input v-model="streamData.unitSymbols" placeholder="请输入单位符号，不超过20个字符" />
        </el-form-item>
      </el-form>
    </template>
    <template #footer>
      <div style="flex: auto">
        <el-button @click="cancelClick">取消</el-button>
        <el-button type="primary" :loading="isButtonLoading" @click="confirmClick(dataStreamFrom)">确认</el-button>
      </div>
    </template>
  </el-drawer>
</template>

<script setup>
import { ref, defineProps, watch, reactive, defineEmits } from 'vue'
import { addDataStream } from '@/apis/dataStream'
import { useRouterStore } from '@/store/router'

const dataStreamFrom = ref(null)
const props = defineProps({
  drawerSwitch: {
    type: Boolean,
    default: false
  },
  drawerTitle: {
    type: String,
    default: 'default-title'
  },
  streamEditData: {
    type: Object,
    default() {
      return {}
    }
  }
})
const emits = defineEmits(['addSuccess', 'close'])
const isShowDrawer = ref(false)
const routerStore = useRouterStore()
const streamData = reactive({
  dataName: '',
  unitSymbols: '',
  unitName: '',
  dataTemplateId: '',
  productId: routerStore.productId
})
const drawerTitle = ref('default_title')
const isEdit = ref(false)
watch(
  () => props.drawerSwitch,
  () => {
    if (JSON.stringify(props.streamEditData) !== '{}') {
      streamData.dataName = props.streamEditData.dataName
      streamData.unitSymbols = props.streamEditData.unitSymbols
      streamData.unitName = props.streamEditData.unitName
      streamData.dataTemplateId = props.streamEditData.dataTemplateId
      isEdit.value = true
      drawerTitle.value = '编辑数据流'
    } else {
      isEdit.value = false
      passDataName.value = true
      drawerTitle.value = '添加数据流'
      streamData.dataTemplateId = ''
      streamData.dataName = ''
      streamData.unitSymbols = ''
      streamData.unitName = ''
      streamData.dataTemplateId = ''
    }
    isShowDrawer.value = true
  }
)

const rules = reactive({
  dataName: [
    { required: true, message: '单位名称不能为空', trigger: 'blur' },
    { max: 20, message: '不能超过20个字符', trigger: 'blur' },
    { validator: checkDataName, trigger: 'blur' }
  ],
  unitName: [
    { required: true, message: '单位名称不能为空', trigger: 'blur' },
    { max: 20, message: '不能超过20个字符', trigger: 'blur' }
  ],
  unitSymbols: [
    { required: true, message: '单位符号不能为空', trigger: 'blur' },
    { max: 20, message: '不能超过10个字符', trigger: 'blur' }
  ]
})

const passDataName = ref(true)
function checkDataName(rule, value, callback) {
  if (passDataName.value) {
    if (/^\w+$/.test(value)) {
      callback()
    } else {
      callback(new Error("数据流名称只能使用字母、数字和'_'"))
    }
  } else {
    callback(new Error('同一产品下存在相同的数据流名称'))
  }
}

function cancelClick() {
  isShowDrawer.value = false
}
const isButtonLoading = ref(false)
function confirmClick(dataStreamFrom) {
  isButtonLoading.value = true
  dataStreamFrom.validate((isValid) => {
    if (isValid) {
      addDataStream(streamData).then(res => {
        if (res.code === 200) {
        //  添加成功
          isShowDrawer.value = false
          emits('addSuccess')
        } else if (res.code === 250) {
          passDataName.value = false
          //  同一个产品下存在相同的dataName，再次验证以显示错误信息
          dataStreamFrom.validateField('dataName', isValid => {
            if (!isValid) {
              passDataName.value = true
            }
          })
        }
      }, rej => {
        console.log(rej)
      }).finally(() => {
        isButtonLoading.value = false
      })
    } else {
      isButtonLoading.value = false
    }
  })
}
function handleClose(dataStreamFrom) {
  isButtonLoading.value = false
  dataStreamFrom.resetFields()
  emits('close')
}
</script>

<style scoped>

</style>
